<template>
  <div class="tweet" :class="altBg ? 'altBackground' : 'normalBackground'">
    <div class="card" v-if="dataReady">
      <div class="cardTitle">
        <img class="avatar" :src="user.avatar" />
        <div class="userInfo">
          <div class="displayName" lang="ja-jp" v-html="user.display"></div>
          <div class="username">@{{ user.name }}</div>
        </div>
      </div>
      <div class="cardBody">
        <div v-html="actualTransText || tweet.content" :lang="actualTransText ? null : 'ja-jp'"></div>
        <div class="origText" v-if="actualTransText && tweet.content">
          <legend>原文</legend>
          <div lang="ja-jp" v-html="tweet.content"></div>
        </div>
      </div>
      <div class="refTweet" v-if="tweet.refTid || tweet.refStatusId">
        <Tweet
          :tid="tweet.refTid"
          :tweets="tweets"
          :statusId="tweet.refStatusId"
          :alt-bg="!altBg"
          :no-date="true"
        />
      </div>
      <div class="media">
        <img v-for="url in JSON.parse(tweet.media)" :key="url" :src="url" />
      </div>
      <div class="postTime" v-if="!noDate">▶ 原推发表于 {{ formatDate(tweet.pubDate) }}</div>
    </div>
    <div v-html="errorMessage" v-if="errorMessage"></div>
  </div>
</template>

<script>
import axios from "axios";
import twemoji from "twemoji";
import escape from "escape-html";
import { v4 as uuidv4 } from "uuid";

export default {
  name: "Tweet",
  props: {
    tid: String,
    statusId: String,
    url: String,
    altBg: Boolean,
    transText: String,
    noDate: Boolean,
    tweets: Object
  },
  data() {
    return {
      tweet: null,
      user: null,
      translation: null,
      dataReady: false,
      actualTransText: null,
      errorMessage: null
    };
  },
  mounted() {
    if (this.tid) {
      axios
        .post("/api_proxy/fridge/db/task/get", {
          taskId: uuidv4(),
          forwardFrom: "twitkit-oven-baker",
          timestamp: new Date().toISOString(),
          data: { tid: this.tid }
        })
        .then(response => {
          if (response.data.code != 0) {
            this.errorMessage = `Fridge返回${response.data.code}：${response.data.message}`;
            return;
          }
          this.renderTweet(response.data.data);
        })
        .catch(error => {
          this.errorMessage = `请求Fridge失败：${error.message}`;
        });
    } else if (this.statusId) {
      this.renderTweet(this.tweets[this.statusId]);
    } else {
      this.errorMessage = "没有传入数据";
    }
  },
  methods: {
    formatDate(date) {
      var padZero = num => {
        if (num < 10) {
          return "0" + num;
        } else {
          return "" + num;
        }
      };
      var formatTimezone = offset => {
        offset = -offset;
        if (offset >= 0) {
          return "UTC+" + offset;
        } else {
          return "UTC" + offset;
        }
      };
      var d = new Date(date);
      var formatted;

      // 实测中遇到了Date解析不出来ISO8601的格式的情况，所以加了下面的代码作为fallback
      if (isNaN(d.getFullYear())) {
        d.setUTCFullYear(parseInt(date.substring(0, 4)));
        d.setUTCMonth(parseInt(date.substring(5, 7)));
        d.setUTCDate(parseInt(date.substring(8, 10)));
        d.setUTCHours(parseInt(date.substring(11, 13)));
        d.setUTCMinutes(parseInt(date.substring(14, 16)));
        d.setUTCSeconds(parseInt(date.substring(17, 19)));

        date = date.substring(19);
        var tzPos = date.indexOf("+");
        if (tzPos == -1) tzPos = date.indexOf("-");
        if (tzPos != -1) {
          var tzStr = date.substring(tzPos).replace(":", "");
          var tzOffset = parseFloat(tzStr);
          if (Math.abs(tzOffset) > 12) tzOffset /= 100;
          tzOffset *= 60 * 60 * 1000;
          d.setTime(d.getTime() - tzOffset);
        }
      }

      formatted =
        `${d.getFullYear()} 年 ${d.getMonth()} 月 ${d.getDate()} 日 ` +
        `${padZero(d.getHours())}:${padZero(d.getMinutes())}:` +
        `${padZero(d.getSeconds())}` +
        `（${formatTimezone(d.getTimezoneOffset() / 60)}）`;

      return formatted;
    },
    renderTweet(tweetData) {
      this.tweet = tweetData.twitter;
      this.user = tweetData.user;
      this.translation = tweetData.translation;
      this.actualTransText =
        this.transText ||
        (this.translation ? this.translation.translation : null);
      if (this.tweet === null) {
        this.errorMessage = "推文不存在";
        return;
      }

      var convertEmoji = str => {
        return twemoji.replace(str, function(emoji) {
          return (
            `<img src="https://twemoji.maxcdn.com/svg/` +
            `${twemoji.convert.toCodePoint(emoji)}.svg" style="height:1em">`
          );
        });
      };

      var addColor = str => {
        str = str.replace(/(@\w{1,15})/g, '<span class="mention">$1</span>');
        str = str.replace(
          /(#[^\s,.!?，。！？<]*)/g,
          '<span class="hashtag">$1</span>'
        );
        str = str.replace(
          /([^"])(https?:\/\/[\w-_~:[\]@!$&'()*+,;=.?%#/]+)([^"])/g,
          '$1<span class="link">$2</span>$3'
        );
        return str;
      };

      if (this.tweet && this.tweet.content)
        this.tweet.content = convertEmoji(addColor(escape(this.tweet.content)));
      if (this.translation && this.translation.content)
        this.translation.content = convertEmoji(
          addColor(escape(this.translation.content))
        );
      if (this.transText)
        this.transText = convertEmoji(addColor(escape(this.transText)));
      this.user.display = convertEmoji(escape(this.user.display));

      this.dataReady = true;
    }
  }
};
</script>

